import React from 'react'
import { makeStyles } from '@material-ui/core/styles';
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';


const useStyles = makeStyles((theme) => ({
  dateBox: {
    display: 'flex',
    flexWrap: 'wrap',
    padding: '20px'
  },
  textField: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
    width: 200,
  },
}));

const Datepicker = (props) => {

    const { date, onDateChange, datasDisponiveis } = props;

    const classes = useStyles();

    const formatDate = (date) => (
      `${date.slice(8,10)}/${date.slice(5,7)}/${date.slice(0,4)}`
    )

    const Picker = () => (
        <form className={classes.dateBox + ' center'} noValidate>
          <Box pb={1}><Typography variant='h7'>Selecione uma data:</Typography></Box>
          
          <select
            id="date"
            label="Data"
            value={date}
            onChange={onDateChange}
            className={classes.textField}
          >
            {datasDisponiveis.map(data => (
              <option key={data} value={data}>{formatDate(data)}</option>
            ))}
          </select>
        </form>
      );

    return (
        <div>
            <Box component={Paper} className="center" style={{ width: '250px', margin: '20px auto'}}>

            <Picker/>

            </Box>
        </div>
    )
}

export default Datepicker
